




import { useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { Dialog, DialogContent, DialogHeader, DialogTitle, DialogClose } from '@/components/ui/dialog';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Plus } from 'lucide-react';
import { createSubTask } from '@/features/subTaskSlice'; // Import the async thunk
import { toast } from 'sonner'; // 

const CreateSubtaskModal = ({ open, setOpen, taskId }) => {
  const [newTitle, setNewTitle] = useState('');
  const dispatch = useDispatch();
  const { loading, error } = useSelector((state) => state.subTask); // Access Redux state
  const isTaskClosed = false; // This should ideally come from props or Redux state

  const handleAdd = async () => {
    if (isTaskClosed || !newTitle.trim()) {
      toast.error('Cannot add subtask: Task is closed or title is empty');
      return;
    }

    try {
      // Dispatch the createSubTask async thunk
      await dispatch(createSubTask({ taskId, subTaskData: { title: newTitle } })).unwrap();
      toast.success('Subtask added successfully');
      setNewTitle('');
      setOpen(false);
    } catch (err) {
      toast.error(error || 'Failed to add subtask');
    }
  };

  return (
    <Dialog open={open} onOpenChange={setOpen}>
      <DialogContent>
        <DialogHeader>
          <DialogTitle className="flex items-center">
            <Plus className="mr-2 h-4 w-4 sm:h-5 sm:w-5 text-green-500" />
            Add New Subtask
          </DialogTitle>
        </DialogHeader>
        <div className="space-y-3">
          <div>
            <span className="font-medium text-xs sm:text-sm">Title:</span>
            <Input
              value={newTitle}
              onChange={(e) => setNewTitle(e.target.value)}
              className="mt-1 text-xs sm:text-sm"
              disabled={isTaskClosed || loading}
              placeholder="Enter subtask title"
            />
          </div>
          {error && <p className="text-red-500 text-xs sm:text-sm">{error}</p>}
          <DialogClose asChild>
            <Button
              onClick={handleAdd}
              disabled={isTaskClosed || loading || !newTitle.trim()}
              className="bg-blue-600 hover:bg-blue-700 text-xs sm:text-sm h-8 sm:h-9"
            >
              {loading ? 'Adding...' : 'Add Subtask'}
            </Button>
          </DialogClose>
        </div>
      </DialogContent>
    </Dialog>
  );
};

export default CreateSubtaskModal;















